import React from 'react';
import { default as MuiStepIcon, StepIconProps as MuiStepIconProps } from '@mui/material/StepIcon';
import SvgIcon, { SvgIconProps } from '@elementor/ui/SvgIcon';
import { useTheme } from '@elementor/ui/styles';

const CheckedCircleIcon = React.forwardRef<SVGSVGElement, SvgIconProps>( ( props, ref ) => {
	const theme = useTheme();

	return (
		<SvgIcon viewBox="0 0 24 24" { ...props } ref={ ref }>
			<g clipPath="url(#clip0_2503_21505)">
				<circle cx="12" cy="12" r="12" fill={ theme.palette.primary.main } />
				<path fill={ theme.palette.primary.contrastText } fillRule="evenodd" clipRule="evenodd" d="M17.7753 8.22472C17.5312 7.98064 17.1355 7.98064 16.8914 8.22472L10.6667 14.4494L7.77532 11.5581C7.53124 11.314 7.13551 11.314 6.89143 11.5581C6.64735 11.8021 6.64735 12.1979 6.89143 12.4419L10.2248 15.7753C10.4688 16.0194 10.8646 16.0194 11.1086 15.7753L17.7753 9.10861C18.0194 8.86453 18.0194 8.4688 17.7753 8.22472Z" />
			</g>
			<defs>
				<clipPath id="clip0_2503_21505">
					<rect width="24" height="24" rx="12" fill="white" />
				</clipPath>
			</defs>
		</SvgIcon>
	);
} );

interface StepIconProps extends MuiStepIconProps {
	as?: React.ElementType;
}

// Modified reason: modifying the completed icon.
const StepIcon = React.forwardRef<HTMLElement, StepIconProps>( ( inProps, ref ) => {
	const props = { ...inProps };

	if ( props.completed ) {
		// Overriding the completed icon using the 'as' prop because Mui does not have a dedicated prop for it.
		// see: https://github.com/mui/material-ui/blob/bcef13eef3208716bc33482dd5af3ac90ae18dc4/packages/mui-material/src/StepIcon/StepIcon.js#L87
		props.as = ( svgProps ) => <CheckedCircleIcon { ...svgProps } />;
	}

	return (
		<MuiStepIcon
			{ ...props }
			ref={ ref as React.MutableRefObject<null> }
		/>
	);
} ) as React.FC<StepIconProps>;

export default StepIcon;
